import React from 'react';
import { Line } from 'react-chartjs-2';
import {
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  LineElement,
  PointElement,
  SubTitle,
  Title,
  Tooltip,
} from 'chart.js';

import grossYearlyLossAgainstDriver from '../data/GrossYearlyLossAgainstDriver.json';
import { COLORS } from '../utils/constatnts';

import '../style.css';
import 'ol/ol.css';

ChartJS.register(
  Tooltip,
  Legend,
  Title,
  SubTitle,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
);

const LineGraph = ({ years, drivers, startYear, endYear }) => {
  const data = {
    datasets: drivers.map((driver, i) => ({
      borderColor: COLORS[i],
      data: years.map((year) => (grossYearlyLossAgainstDriver[driver][year] / 100000).toFixed(2)),
      fill: false,
      label: driver,
      tension: 0.4,
    })),
    labels: years,
  };

  const options = {
    plugins: {
      legend: {
        display: true,
        labels: {
          padding: 20,
        },
        position: 'right',
      },
      subtitle: {
        display: true,
        padding: {
          bottom: 12,
        },
        text: '(Values shown in 100,000 hectares)',
      },
      title: {
        display: true,
        text: `Factors driving deforestation between ${startYear} and ${endYear}`,
      },
    },
    scales: {
      x: { type: 'category' },
    },
  };

  return (
    <div className="line-graph">
      <Line data={data} options={options} />
    </div>
  );
};

export default LineGraph;
