import React from 'react';

import { toTwoDecimalPlaces } from '../utils/helperFunctions';

import 'ol/ol.css';
import '../style.css';

const MapToolTip = ({ toolTipRef, country, data }) => {
  const toolTipData = {
    'Change (%)': data.percentage_change,
    Disturbed: data.disturb,
    Gained: data.gain,
    Lost: data.loss,
    'Total Area': data.total_area,
  };

  return (
    <div ref={toolTipRef} id="info">
      <p className="country-name">{country}</p>
      {Object.keys(toolTipData).map((key) => (
        <div key={key}>
          <p className="info-type">{key}</p>
          <span>: {toTwoDecimalPlaces(toolTipData[key])}</span>
        </div>
      ))}
    </div>
  );
};

export default MapToolTip;
