import React, { useState } from 'react';
import {
  ArcElement,
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
} from 'chart.js';

import DeforestationMap from './components/DeforestationMap';
import DoubleSlider from './components/DoubleSlider';
import DoughnutChart from './components/DoughnutChart';
import LineGraph from './components/LineGraph';
import grossYearlyLossAgainstDriver from './data/GrossYearlyLossAgainstDriver.json';
import { END_YEAR, START_YEAR } from './utils/constatnts';

import './style.css';
import 'ol/ol.css';

ChartJS.register(
  ArcElement,
  Tooltip,
  Legend,
  Title,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
);

const MapComponent = () => {
  const [startYear, setStartYear] = useState(START_YEAR);
  const [endYear, setEndYear] = useState(END_YEAR);

  const drivers = Object.keys(grossYearlyLossAgainstDriver);
  const years = new Array(endYear + 1 - startYear).fill(true).map((_, i) => i + startYear);

  const onSliderChange = ([start, end]) => {
    setStartYear(start);
    setEndYear(end);
  };

  return (
    <div className="container">
      <DeforestationMap />

      <div className="statistics-pane">
        <DoughnutChart drivers={drivers} endYear={endYear} startYear={startYear} />
        <LineGraph endYear={endYear} startYear={startYear} years={years} drivers={drivers} />
        <DoubleSlider onChange={onSliderChange} />
      </div>
    </div>
  );
};

export default MapComponent;
