import React, { useEffect, useMemo, useRef, useState } from 'react';
import FullScreen from 'ol/control/FullScreen';
import GeoJSON from 'ol/format/GeoJSON';
import ImageLayer from 'ol/layer/Image';
import VectorLayer from 'ol/layer/Vector';
import Map from 'ol/Map';
import ImageArcGISRest from 'ol/source/ImageArcGISRest';
import VectorSource from 'ol/source/Vector';
import { Fill, Stroke, Style } from 'ol/style';
import View from 'ol/View';

import loadingAnimation from '../assets/loadingAnimation.gif';
import aggregatedCountryForestLoss from '../data/AggregatedCountryForestLossStats.json';
import { END_YEAR, MAP_SERVERS, START_YEAR } from '../utils/constatnts';

import MapToolTip from './MapToolTip';
import Slider from './Slider';

import 'ol/ol.css';
import '../style.css';

const DeforestationMap = () => {
  const mapRef = useRef(null);
  const toolTipRef = useRef(null);

  const [loading, setLoading] = useState(false);
  const [mapYear, setMapYear] = useState(START_YEAR);
  const [countryCode, setCountryCode] = useState('');
  const [country, setCountry] = useState('');
  const [countryStats, setCountryStats] = useState({});

  const highlightCountryBoundaries = (feature) => {
    const style = new Style({
      fill: new Fill({
        color: 'rgba(121, 85, 72, 0.4)',
      }),
      stroke: new Stroke({
        color: 'rgba(121, 85, 72, 0.2)',
        width: 1,
      }),
    });

    feature.setStyle(style);
  };

  const resetHighlightedCountry = (feature) => feature?.setStyle(null);

  const createImageLayer = useMemo(
    () => (image) =>
      new ImageLayer({
        className: `masked-image-${image.filter || 0}`,
        opacity: 1,
        source: new ImageArcGISRest({
          ratio: 1,
          url: `https://maps.globilportal.org/server/rest/services/Forests/${image.name}/${image.serverType}`,
        }),
      }),
    [],
  );

  const countryBoundariesLayerSource = useMemo(
    () =>
      new VectorSource({
        format: new GeoJSON(),
        url: 'https://raw.githubusercontent.com/datasets/geo-countries/master/data/countries.geojson',
      }),
    [],
  );

  const countryBoundariesLayer = useMemo(
    () =>
      new VectorLayer({
        source: countryBoundariesLayerSource,
        style: new Style({
          stroke: new Stroke({ color: 'rgba(121, 85, 72, 0.2)', width: 1 }),
        }),
      }),
    [countryBoundariesLayerSource],
  );

  const baseLayer = useMemo(() => {
    return new VectorLayer({
      background: 'rgba(121, 85, 72, 0.1)',
      source: countryBoundariesLayerSource,
      style: new Style({
        fill: new Fill({ color: '#e7e7e7' }),
      }),
    });
  }, [countryBoundariesLayerSource]);

  let currentFeature;
  const displayFeatureInfo = (pixel, target, map) => {
    const feature = target.closest('.ol-control')
      ? undefined
      : map?.forEachFeatureAtPixel(pixel, (feature) => feature);

    if (toolTipRef.current) {
      if (feature) {
        toolTipRef.current.style.left = `${pixel[0]}px`;
        toolTipRef.current.style.top = `${pixel[1]}px`;
        if (feature !== currentFeature) {
          highlightCountryBoundaries(feature);
          toolTipRef.current.style.visibility = 'visible';
          setCountryCode(feature?.values_?.ISO_A3 || '');
          setCountry(feature?.values_?.ADMIN || '');
        }
      } else {
        toolTipRef.current.style.visibility = 'hidden';
      }
    }

    if (feature !== currentFeature) resetHighlightedCountry(currentFeature);

    currentFeature = feature;
  };

  const view = useMemo(
    () =>
      new View({
        center: [0, 0],
        zoom: 2,
      }),
    [],
  );

  const deforestationLayers = useMemo(() => MAP_SERVERS.map(createImageLayer), [createImageLayer]);

  const calculateLayerOpacity = (layerIndex) => {
    let opacity = 1;
    const yearDifference = END_YEAR - Math.floor(mapYear) - 1;
    const transparentLayers = Math.floor(yearDifference > 0 ? yearDifference / 2 : 0);
    const opacityCoefficient = mapYear - (END_YEAR - 2 * (transparentLayers + 1));
    const layerNumber = 9 - layerIndex;

    if (transparentLayers < layerNumber) opacity = 0;
    if (transparentLayers === layerNumber) opacity = 1 - opacityCoefficient / 2;
    if (layerIndex === MAP_SERVERS.length - 1) opacity = 1;

    return opacity;
  };

  const setupMap = () => {
    const map = new Map({
      layers: [baseLayer],
      target: mapRef.current,
      view,
    });

    map.addControl(new FullScreen());

    deforestationLayers.forEach((layer, i) => {
      layer.setOpacity(calculateLayerOpacity(i));
      map.addLayer(layer);
    });

    map.addLayer(countryBoundariesLayer);

    map.on('pointermove', (evt) => {
      if (evt.dragging && toolTipRef.current) {
        toolTipRef.current.style.visibility = 'hidden';
        currentFeature = undefined;
        return;
      }
      const pixel = map.getEventPixel(evt.originalEvent);
      displayFeatureInfo(pixel, evt.originalEvent.target, map);
    });

    map.on('click', (evt) => {
      displayFeatureInfo(evt.pixel, evt.originalEvent.target, map);
    });

    map.getTargetElement().addEventListener('pointerleave', () => {
      currentFeature = undefined;
      if (toolTipRef.current) toolTipRef.current.style.visibility = 'hidden';
    });

    map.on('loadstart', () => setLoading(true));

    map.on('loadend', () => setLoading(false));

    // return map's teardown function
    return () => map.dispose();
  };

  useEffect(() => {
    if (countryCode) {
      const stats = aggregatedCountryForestLoss[countryCode] || {};
      setCountryStats(stats);
    }
  }, [countryCode]);

  useEffect(() => {
    if (!mapRef.current || !toolTipRef.current) return;
    return setupMap();
  }, [mapYear]);

  return (
    <div className="map-container">
      <p className="map-title">Tree cover as of the year {Math.floor(mapYear)}</p>
      <div ref={mapRef} id="map">
        {loading && <img className="loading-animation" src={loadingAnimation} alt="Loading..." />}
        <Slider onChange={setMapYear} />
        <MapToolTip toolTipRef={toolTipRef} country={country} data={countryStats} />
      </div>
    </div>
  );
};

export default DeforestationMap;
