import React from 'react';
import ReactSlider from 'react-slider';
import {
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  LineElement,
  PointElement,
  SubTitle,
  Title,
  Tooltip,
} from 'chart.js';

import { END_YEAR, START_YEAR } from '../utils/constatnts';

import '../style.css';
import 'ol/ol.css';

ChartJS.register(
  Tooltip,
  Legend,
  Title,
  SubTitle,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
);

const DoubleSlider = ({ onChange }) => {
  return (
    <>
      <ReactSlider
        className="chart-year-range-slider"
        thumbClassName="chart-year-range-thumb"
        trackClassName="chart-year-range-track"
        defaultValue={[START_YEAR, END_YEAR]}
        min={START_YEAR}
        max={END_YEAR}
        ariaLabel={['Lower thumb', 'Upper thumb']}
        ariaValuetext={(state) => `Thumb value ${state.valueNow}`}
        renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
        pearling
        marks
        onChange={onChange}
        minDistance={1}
      />
      <p className="slider-subtext">
        (Move this sliders to change the year range between <b>2001</b> and <b>2022</b>)
      </p>
    </>
  );
};

export default DoubleSlider;
