import React from 'react';
import ReactSlider from 'react-slider';
import {
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LinearScale,
  LineElement,
  PointElement,
  SubTitle,
  Title,
  Tooltip,
} from 'chart.js';

import { END_YEAR, START_YEAR } from '../utils/constatnts';

import '../style.css';
import 'ol/ol.css';

ChartJS.register(
  Tooltip,
  Legend,
  Title,
  SubTitle,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
);

const Slider = ({ onChange }) => (
  <div
    style={{ bottom: -60, height: 60, left: '5%', position: 'absolute', width: `90%`, zIndex: 99 }}
  >
    <ReactSlider
      className="map-year-slider"
      thumbClassName="map-year-slider-thumb"
      trackClassName="map-year-slider-track"
      renderThumb={(props, state) => <div {...props}>{Math.floor(state.valueNow)}</div>}
      min={START_YEAR}
      defaultValue={START_YEAR}
      max={END_YEAR}
      onChange={onChange}
      step={0.2}
    />
    <p className="slider-subtext">
      (Move this slider to see tree cover loss between the years <b>2001</b> and <b>2022</b>)
    </p>
  </div>
);

export default Slider;
