import React from 'react';
import { Doughnut } from 'react-chartjs-2';
import { Chart as ChartJS, Legend, Title, Tooltip } from 'chart.js';

import grossYearlyLossAgainstDriver from '../data/GrossYearlyLossAgainstDriver.json';
import { COLORS } from '../utils/constatnts';
import { aggregate } from '../utils/helperFunctions';

import '../style.css';
import 'ol/ol.css';

ChartJS.register(Tooltip, Legend, Title);

const DoughnutChart = ({ drivers, startYear, endYear }) => {
  const lossPerDriver = drivers.map((driver) =>
    aggregate(grossYearlyLossAgainstDriver[driver], startYear, endYear),
  );

  const data = {
    datasets: [
      {
        backgroundColor: COLORS,
        borderColor: 'white',
        borderWidth: 1,
        data: lossPerDriver,
        label: 'Tree loss in Hectares',
      },
    ],
    labels: drivers,
  };

  const options = {
    plugins: {
      legend: {
        align: 'center',
        display: true,
        position: 'right',
      },
      subtitle: {
        display: true,
        padding: {
          bottom: 12,
        },
        text: '(Values shown in hectares)',
      },
      title: {
        display: true,
        text: `Deforestation driver from ${startYear} to ${endYear}`,
      },
    },
    responsive: true,
  };

  return (
    <div className="doughnut">
      <Doughnut data={data} options={options} />
    </div>
  );
};

export default DoughnutChart;
