import { END_YEAR, START_YEAR } from './constatnts';

export const aggregate = (dataset, yearStart = START_YEAR, yearEnd = END_YEAR) => {
  let sum = 0;

  Object.keys(dataset).forEach((year) => {
    if (year >= yearStart && year <= yearEnd) {
      sum += dataset[year];
    }
  });

  return sum;
};

export const toTwoDecimalPlaces = (data) => (data || 0).toFixed(2);
